@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Source Sans Pro', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #1a1a1c;
  color: white;
  text-align: center;
}
.coin-app{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: #fff;
  margin-top: 64px;
  -webkit-justify-content: center;
          justify-content: center;
}
.text{
  margin-bottom: 32px;
  text-align: center;
}
.coin-input{
  color: #e2e2e2;
  width: 300px;
  height: 50px;
  padding: 0px 5px;
  border: none;
  background-image: linear-gradient(-220deg,rgb(10, 119, 243) 0%,rgb(105, 18, 219) 40%,rgb(191, 17, 226) 100%);
  border-radius: 5px;
}
.coin-input::-webkit-input-placeholder{
  color: white;
  font-size: 14px;
}
.coin-input:-ms-input-placeholder{
  color: white;
  font-size: 14px;
}
.coin-input::placeholder{
  color: white;
  font-size: 14px;
}

.coin-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}
.coin-row{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    height: 100px;
    width: 900px;
    border-bottom: 1px solid #636262;
}
.coin{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding-right: 6px;
    min-width: 300px;
}
.coin h1{
    font-size: 16px;
    width: 150px;
}
.coin img{
    height: 30px;
    width: 30px;
}
.coin-symbol{
    text-transform: uppercase;
}
.coin-data{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
}
.price-change{
    width: 110px;
}
.coin-volume{
    width: 200px;
}
.coin-market-cap{
    width: 240px;
}
.coin-percent{
    width: 80px;
}
.red{
    color:rgb(212, 14, 14);
}
.green{
    color: rgb(23, 207, 23);
}
.text{
    font-size: 32px;
}
