.coin-container{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.coin-row{
    display: flex;
    justify-content: start;
    align-items: center;
    height: 100px;
    width: 900px;
    border-bottom: 1px solid #636262;
}
.coin{
    display: flex;
    align-items: center;
    padding-right: 6px;
    min-width: 300px;
}
.coin h1{
    font-size: 16px;
    width: 150px;
}
.coin img{
    height: 30px;
    width: 30px;
}
.coin-symbol{
    text-transform: uppercase;
}
.coin-data{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.price-change{
    width: 110px;
}
.coin-volume{
    width: 200px;
}
.coin-market-cap{
    width: 240px;
}
.coin-percent{
    width: 80px;
}
.red{
    color:rgb(212, 14, 14);
}
.green{
    color: rgb(23, 207, 23);
}
.text{
    font-size: 32px;
}