@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');
*{
  font-family: 'Source Sans Pro', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #1a1a1c;
  color: white;
  text-align: center;
}
.coin-app{
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #fff;
  margin-top: 64px;
  justify-content: center;
}
.text{
  margin-bottom: 32px;
  text-align: center;
}
.coin-input{
  color: #e2e2e2;
  width: 300px;
  height: 50px;
  padding: 0px 5px;
  border: none;
  background-image: linear-gradient(-220deg,rgb(10, 119, 243) 0%,rgb(105, 18, 219) 40%,rgb(191, 17, 226) 100%);
  border-radius: 5px;
}
.coin-input::placeholder{
  color: white;
  font-size: 14px;
}
